<template>
  <navigation-bar go-back="/profile">
    <div slot="middle_content">
      登录
    </div>
  </navigation-bar>
  <div class="weui_cells weui_cells_form">
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <label class="weui_label">手机号</label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="number" pattern="[0-9]*" v-model="mobile"/>
      </div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <label class="weui_label">密码</label>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <input class="weui_input" type="text" v-model="password" />
      </div>
    </div>
  </div>
  <div style="width:100%;height:30px;"></div>
  <div class="bd spacing">
    <a href="javascript:;" @click="login" class="weui_btn weui_btn_primary">登录</a>
  </div>
  <div style="width:100%;height:100px;"></div>
  <div class="signin_line"><span>第三方登录</span></div>
  <div style="width:100%;height:30px;"></div>
  <div class="oauth_logos">
    <div class="weui_grids">
      <a href="javascript:;" @click="weixinOauth" class="weui_grid">
        <div class="weui_grid_icon">
          <img src="../assets/weixin.png" alt="">
        </div>
        <p class="weui_grid_label">
          微信
        </p>
      </a>
      <a href="javascript:;" class="weui_grid">
        <div class="weui_grid_icon">
          <img src="../assets/icon_nav_msg.png" alt="">
        </div>
        <p class="weui_grid_label">
          QQ
        </p>
      </a>
      <a href="javascript:;" class="weui_grid">
        <div class="weui_grid_icon">
          <img src="../assets/icon_nav_msg.png" alt="">
        </div>
        <p class="weui_grid_label">
          微博
        </p>
      </a>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        user: {
          mobile: '',
          password: ''
        }
      }
    },
    route: {
      data (transition) {
        let access_token = localStorage.getItem('jc_user_access_token')
        if (access_token != null && access_token !== undefined) {
          this.$route.router.go({ name: 'profile' })
        }
      }
    },
    methods: {
      login () {
        var formData = new FormData()
        formData.append('mobile', localStorage.jc_user_access_token)
        formData.append('password', this.booking_time)
        this.$http.post('http://jiancan.me/api/u1/users/current.json', formData).then(function (response) {
          localStorage.setItem('jc_user_access_token', response.data.access_token)
          this.$route.router.go({ name: 'profile' })
        }, function (response) {
          this.$dispatch('response-msg', response)
        })
      },
      weixinOauth () {
        window.location.href = 'http://jiancan.me/weixin/authorize?request_url=profile'
      }
    },
    components: {
      'NavigationBar': require('../components/navigation-bar.vue')
    }
  }
</script>



